import { TaskDocument } from "../documents/TaskDocument";

export default class Documents {
	static openDocument(id: string, type: string, datas: any) {
		const documentData = this.getDocument(id, datas);
		switch (type) {
			case "tasksDocuments":
				const doc = new TaskDocument(documentData.id, documentData.title, documentData.description, documentData.task);
		}
	}

	static async getDocumentDatas(type: string) {
		const data = await window.documentDatas.getDatas(type);
		return data;
	}

	static getDocument(id: string, datas: any): any {
		let data = {};
		datas.forEach((d: any) => {
			if (d.id === id) {
				data = d;
			}
		});

		return data;
	}
}
